.app__login-container{
    height: 100vh;
    width: 100%;
    background-image: url(../../assets/login-bg.png);
    background-size:cover;
    background-repeat: no-repeat ;
    background-position: center;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .login-form{
        background-color: var(--gray-color);
        width: 17rem;
        padding: 2rem;
        border-radius: 1rem;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
        z-index: 1;

        .profile-img{
            width: 5rem;
            height: 5rem;
            margin: auto;
            background-color: green;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: -4rem;
            margin-bottom: 2rem;

            svg{
                font-size: 3rem;
                fill: var(--gray-color);
            }

        }
        .email-input{
            margin-bottom: .5rem;
        }
        .input{
            display: flex;
            position: relative;
            .under-line{
                width: 100%;
                height: 3px;
                background-color: rgb(0, 53, 0);
                position: absolute;
                bottom: 0;
                transition: .5s ease;
            }
            .icon{
                width: 2rem;
                height: 2rem;
                background-color: rgb(0, 53, 0);
                display: flex;
                justify-content: center;
                align-items: center;

                svg{
                    fill: var(--gray-color);
                }
            }
            input{
                width: 90%;
                background-color: green;
                border: none;
                padding-left: 1rem;

                &::placeholder{
                    color: var(--gray-color);
                }
                &:focus{
                    outline: transparent;

                }
            }
        }
        .setting{
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 2rem;
            p{
                display: flex;
                flex-direction: row;
                justify-content: center;

                input{
                    margin-right: 3px;
                    background: transparent;
                }
            }
            p,a{
                font-size: .6rem;
                text-decoration: none;
                color: black;
            }
            .chech-wrapper{
                display: block;
                position: relative;
                padding-left: 15px;
                cursor: pointer;
                font-size: 22px;
                -webkit-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;

                input {
                    position: absolute;
                    opacity: 0;
                    cursor: pointer;
                    height: 0;
                    width: 0;
                }  
                .checkmark {
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 10px;
                    width: 10px;
                    border: solid 1px black;

                }          
                input:checked ~ .checkmark {
                    background-color: black;
                } 
                .container:hover input ~ .checkmark {
                    background-color: #ffffff;
                }                       
            }
        }
    }
    .login-btn{
        width: 10rem;
        height: 3rem;
        margin-top: -1rem;
        padding-top: 1.3rem;
        display: flex;
        cursor: pointer;
        justify-content: center;
        font-weight: 600;
        background-color: var(--gray-color);
        box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
        border-bottom-left-radius: 1rem;
        border-bottom-right-radius: 1rem;
    }
}