.reach-wrapper{
    .card {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        border-radius: 0.75rem;
        background-color: white;
        width: 300px;
        height: 410px;
        box-shadow: 0 4px 6px -1px rgba(0,0,0,.1),
        0 2px 4px -2px rgba(0,0,0,.1);
        margin: 1rem;
      }
      
      .header {
        position: relative;
        background-clip: border-box;
        margin-top: 1.5rem;
        margin-left: 1rem;
        margin-right: 1rem;
        border-radius: 0.75rem;
        background-color: rgb(33 ,150, 243);
        box-shadow: 0 10px 15px -3px rgba(33,150,243,.4),0 4px 6px -4px rgba(33,150,243,.4);
        height: 14rem;
      }
      
      .info {
        border: none;
        padding: 1.5rem;
        text-align: center;
      }
      
      .title {
        color: rgb(38, 50, 56);
        letter-spacing: 0;
        line-height: 1.375;
        font-weight: 600;
        font-size: 1rem;
        margin-bottom: 0.5rem;
        text-align: left;
      }
      
      .footer {
        padding: 0.75rem;
        border: 1px solid rgb(236 ,239, 241);
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: rgba(0, 140, 255, 0.082);
      }
      
      .tag {
        font-weight: 300;
        font-size: .75rem;
        display: block;
      
      }
      
      .action {
        -webkit-user-select: none;
        -moz-user-select: none;
        user-select: none;
        border: none;
        outline: none;
        box-shadow: 0 4px 6px -1px rgba(33,150,243,.4),0 2px 4px -2px rgba(33,150,243,.4);
        color: rgb(255 ,255, 255);
        text-transform: uppercase;
        font-weight: 700;
        font-size: .75rem;
        padding: 0.75rem 1.5rem;
        background-color: rgb(33 ,150, 243);
        border-radius: 0.5rem;
    }
    button {
        width: 150px;
        height: 50px;
        cursor: pointer;
        display: flex;
        align-items: center;
        background: red;
        border: none;
        border-radius: 5px;
        box-shadow: 1px 1px 3px rgba(0,0,0,0.15);
        background: #e62222;
        padding: 0;
        position: relative;
       }
       
       button, button span {
        transition: 200ms;
       }
       
       button .text {
        transform: translateX(35px);
        color: white;
        font-weight: bold;
       }
       
       button .icon {
        position: absolute;
        border-left: 1px solid #c41b1b;
        transform: translateX(110px);
        height: 40px;
        width: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
       }
       
       button svg {
        width: 15px;
        fill: #eee;
       }
       
       button:hover {
        background: #ff3636;
       }
       
       button:hover .text {
        color: transparent;
       }
       
       button:hover .icon {
        width: 150px;
        border-left: none;
        transform: translateX(0);
       }
       
       button:focus {
        outline: none;
       }
       
       button:active .icon svg {
        transform: scale(0.8);
       }  
      
}