.Links-wrapper{
    position: absolute;
    top: 0;
    left: 22rem;
    height: 100vh;
    @media only screen and (max-width: 576px){
        position: static;
        width: 100vw;
      }
    #sidebar{
        height: 100vh;
        h2{
            margin-top: 1rem;
        }

        .input {
            border-radius: 10px;
            outline: 2px solid var(--secondary-color);
            border: 0;
            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
            background-color: #e2e2e2;
            outline-offset: 3px;
            padding: 10px 1rem;
            transition: 0.25s;
            width: 100%;
            margin:auto;
            margin-top: 1rem;

        }
          
        .input:focus {
            outline-offset: 5px;
            background-color: #fff
        }
    }
    .button-wrapper{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 1rem 0;
    }
    button {
        padding: 15px 25px;
        border: unset;
        border-radius: 15px;
        color: #212121;
        z-index: 1;
        background: #e8e8e8;
        position: relative;
        font-weight: 1000;
        font-size: 17px;
        -webkit-box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27);
        box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27);
        transition: all 250ms;
        overflow: hidden;
    }
       
    button::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 0;
        border-radius: 15px;
        background-color: #212121;
        z-index: -1;
        -webkit-box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27);
        box-shadow: 4px 8px 19px -3px rgba(0,0,0,0.27);
        transition: all 250ms
    }
       
    button:hover {
        color: #e8e8e8;
    }
       
    button:hover::before {
        width: 100%;
    }
}