.delete-wrapper{
    width: 22rem;
    background-color: var(--primary-color);
    border-radius: 1rem;
    text-align: center;

    .icon{
        width: 100%;
        margin-top: 1rem;

        img{
            width: 5rem;
        }
    }
    h2{
        padding: 1rem;
    }
    p{
        margin-top: -1rem;
    }
    .btn-wrapper{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-around;
        margin: 1rem 0;

        button{
            width: 8rem;
            cursor: pointer;
        }
        .btn-delete{
            background-color: rgb(159, 9, 9);
            color: var(--primary-color);
        }
    }
}