$theme:       #454cad;
$dark-text:   #5f6982;

#contact-form{
    width: 800px;
    background-color: var(--primary-color);
    @media only screen and (max-width: 576px){
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      overflow: scroll;
    }

    .title-wrapper{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1rem;
        border-bottom: var(--gray-color) solid 1px;
        font-weight: 600;
    }
    .marg{
        width: 100%;
        height: 3rem;
        background: var(--primary-color);
    }
    .input-wrapper{
      width: 100%;
      display: flex;
      flex-direction: row-reverse;
      flex-wrap: wrap;
      >div{
        min-width: 20rem;
        @media only screen and (max-width: 576px){
          margin: auto;
        }
      }
      
    }

    .text-input{
        width: 50%;
        
        .data-name{
            width: 100%;
            height: 3rem;

            ul{
                display: flex;
                justify-content: flex-start;
                color: var(--gray-color);

                li{
                    list-style: none;
                    padding: .3rem;
                    cursor: pointer;
                }
                .active{
                    border-bottom: solid 2px #00c3ff;
                    font-weight: 600;
                    color: var(--secondary-color);
                }
            }
        }
        label{
            display: flex;
            flex-direction: column;
            font-weight: 600;
            color: var(--secondary-color);

            span{
                padding: .5rem;
            }
            input,textarea{
                width: 90%;
            }
        }
        p{
            margin: 1rem;
            margin-top: 2rem;
            display: flex;
            align-items: center;
            justify-content: flex-start;

            button[type=submit]{
                margin-right: 1rem;
                width: 8rem;
                background-color: #3992ff;
                color: var(--primary-color);
            }
        }
    }

    
  .input-files{
    display: flex;
  }
}

///////////////////////////img-upload
.img-input-wrapper{
  width: 50%;
  .file-change{
    width: 100%;
    display: flex;
    justify-content: space-around;
    >div{
      width: 5rem;
      height: 5rem;
      border: solid 1px var(--gray-color);
      border-radius: 1rem;
      cursor: pointer;
      img{
        width: 100%;
        height: 100%;
        object-fit: fill;
        border-radius: 1rem;
        border: none;
        
      }
      video{
        width: 100%;
        height: 100%;
        object-fit: fill;
      }
    }
  }
}
.img-input{
    width: 100%;
    padding: 1rem;
    align-items: center;
    display: flex;
    p{
      font-size: 1.2rem;
      font-weight: 600;
      margin-left: 1rem;
    }
    .uploader {
        display: block;
        clear: both;
        margin: 0 auto;
        width: 100%;
        max-width: 600px;
      
        label {
          float: left;
          clear: both;
          width: 100%;
          padding: 2rem 1.5rem;
          text-align: center;
          background: #fff;
          border-radius: 7px;
          border: 3px solid #eee;
          transition: all .2s ease;
          user-select: none;
      
          &:hover {
            border-color: $theme;
          }
          &.hover {
            border: 3px solid $theme;
            box-shadow: inset 0 0 0 6px #eee;
            
            #start {
              svg{
                transform: scale(0.8);
                opacity: 0.3;
              }
            }
          }
        }
      
        #start {
          float: left;
          clear: both;
          width: 100%;
          
          svg {
            font-size: 50px;
            margin-bottom: 1rem;
            transition: all .2s ease-in-out;
          }
        }
        #response {
          float: left;
          clear: both;
          width: 100%;
          #messages {
            margin-bottom: .5rem;
          }
        }
      
        #file-image {
          display: inline;
          margin: 0 auto .5rem auto;
          width: auto;
          height: auto;
          max-width: 180px;
        }
        
        #notimage {
          display: block;
          float: left;
          clear: both;
          width: 100%;
        }
      
        progress,
        .progress {
          // appearance: none;
          display: inline;
          clear: both;
          margin: 0 auto;
          width: 100%;
          max-width: 180px;
          height: 8px;
          border: 0;
          border-radius: 4px;
          background-color: #eee;
          overflow: hidden;
        }
      
        .progress[value]::-webkit-progress-bar {
          border-radius: 4px;
          background-color: #eee;
        }
      
        .progress[value]::-webkit-progress-value {
          background: linear-gradient(to right, darken($theme,8%) 0%, $theme 50%);
          border-radius: 4px; 
        }
        .progress[value]::-moz-progress-bar {
          background: linear-gradient(to right, darken($theme,8%) 0%, $theme 50%);
          border-radius: 4px; 
        }
      
        input[type="file"] {
          display: none;
        }
      
        div {
          margin: 0 0 .5rem 0;
          color: $dark-text;
        }
        .btn {
          display: inline-block;
          margin: .5rem .5rem 1rem .5rem;
          clear: both;
          font-family: inherit;
          font-weight: 700;
          font-size: 14px;
          text-decoration: none;
          text-transform: initial;
          border: none;
          border-radius: .2rem;
          outline: none;
          padding: 0 1rem;
          height: 36px;
          line-height: 36px;
          color: #fff;
          transition: all 0.2s ease-in-out;
          box-sizing: border-box;
          background: $theme;
          border-color: $theme;
          cursor: pointer;
        }
    }   
}

/////////////////////loader
.loader {
  width: 64px;
  height: 64px;
  position: relative;
  background: #FFF;
  border-radius: 4px;
  overflow: hidden;
  margin: auto !important;
}

.loader:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 40px;
  height: 40px;
  transform: rotate(45deg) translate(30%, 40%);
  background: #ff9371;
  box-shadow: 32px -34px 0 5px #ff3d00;
  animation: slide 2s infinite ease-in-out alternate;
}

.loader:after {
  content: "";
  position: absolute;
  left: 10px;
  top: 10px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #ff3d00;
  transform: rotate(0deg);
  transform-origin: 35px 145px;
  animation: rotate 2s infinite ease-in-out;
}

@keyframes slide {
  0% , 100% {
    bottom: -35px
  }

  25% , 75% {
    bottom: -2px
  }

  20% , 80% {
    bottom: 2px
  }
}

@keyframes rotate {
  0% {
    transform: rotate(-15deg)
  }

  25% , 75% {
    transform: rotate(0deg)
  }

  100% {
    transform: rotate(25deg)
  }
}
  