.team-header{
  width: 100%;
  padding: 1rem;
  display: none;
  @media only screen and (max-width: 576px){
    display: block;
  }
  .icon{
    width: 3rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid 1px var(--gray-color);
    border-radius: 1rem;
    margin-left:2rem;

    svg{
      color: black;
      font-size: 1rem;
    }
  }
}
.contact-wrapper{
    flex-grow: 1;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    
    @media only screen and (max-width: 576px){
      justify-content: center;
    }
}
#contact {
    display: flex;
    margin: 1rem;
    align-items: center;
    flex-wrap: wrap;
    @media only screen and (max-width: 576px){
      .dsc-wrapper {
        width: 100% !important;
      }
      .image-wrapper{
        display: flex;
        width: 100%;
        justify-content: center;
        margin-bottom: 1rem;
        img{
          margin-right: 0;
        }
      }
    }
    .dsc-wrapper {
      width: 50%;
    }
  }
  
  #contact h1 {
    font-size: 2rem;
    font-weight: 700;
    margin: 0;
    line-height: 1.2;
    @media only screen and (max-width: 576px){
      font-size: 1.7rem;
    }
  }
  
  #contact h1 + p {
    margin: 0;
  }
  
  #contact h1 + p + p {
    white-space: break-spaces;
  }
  
  #contact h1:focus {
    outline: none;
    color: hsl(224, 98%, 58%);
  }
  
  #contact a[href*="twitter"] {
    display: flex;
    font-size: 1.5rem;
    color: #3992ff;
    text-decoration: none;
  }
  #contact a[href*="twitter"]:hover {
    text-decoration: underline;
  }
  
  #contact img {
    width: 12rem;
    height: 12rem;
    background: #c8c8c8;
    margin-right: 2rem;
    border-radius: 1.5rem;
    object-fit: cover;
    @media only screen and (max-width: 576px){
      width: 10rem;
      height: 10rem;
    }
  }
  
  #contact h1 ~ div {
    display: flex;
    gap: 0.5rem;
    margin: 1rem 0;
  }
  


