.email-wrapper{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    @media only screen and (max-width: 576px){
        justify-content: center;
    }
    .email-container{
        border: 1px solid var(--secondary-color);
        width: 20rem;
        display: flex;
        padding: .5rem;
        border-radius: 2rem;
        align-items: center;
        svg{
            font-size: 2rem;
            margin: 0 1rem;
            margin-top: .4rem;
        }
        p{
            font-weight: 600;
        }
    }
    button {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 1em;
        border: 0px solid transparent;
        background-color: rgba(237, 77, 77, 0.08);
        border-radius: 1.25em;
        transition: all 0.2s linear;
        height: 3rem;
        width: 3rem;
        margin-left: 1rem;
        @media only screen and (max-width: 576px){
            margin-top: 1rem;
        }
        svg{
            margin-top: 5px;
            fill: red ;
        }
    }
       
    button:hover {
        box-shadow: 3.4px 2.5px 4.9px rgba(0, 0, 0, 0.025),
         8.6px 6.3px 12.4px rgba(0, 0, 0, 0.035),
         17.5px 12.8px 25.3px rgba(0, 0, 0, 0.045),
         36.1px 26.3px 52.2px rgba(0, 0, 0, 0.055),
         99px 72px 143px rgba(0, 0, 0, 0.08);
    }
       
    .tooltip {
        position: relative;
        display: inline-block;
    }
       
    .tooltip .tooltiptext {
        visibility: hidden;
        width: 4em;
        background-color: rgba(0, 0, 0, 0.253);
        color: #fff;
        text-align: center;
        border-radius: 6px;
        padding: 5px 0;
        position: absolute;
        z-index: 1;
        top: 25%;
        left: 110%;
        margin-top: -.4rem;
    }
       
    .tooltip .tooltiptext::after {
        content: "";
        position: absolute;
        top: 50%;
        right: 100%;
        margin-top: -5px;
        border-width: 5px;
        border-style: solid;
        border-color: transparent rgba(0, 0, 0, 0.253) transparent transparent;
    }
       
    .tooltip:hover .tooltiptext {
        visibility: visible;
    }
}